body {
  background-color: #fff;
  margin: 0;
  padding: 2rem;
  font-family: Montserrat, sans-serif;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 2rem;
}

.chalk-title {
  color: #fff;
  text-align: center;
  text-shadow: 1px 1px 2px #000;
  margin-top: 5px;
  margin-bottom: 5px;
  font-family: Montserrat, sans-serif;
  font-size: 3rem;
}

input, select, button {
  box-sizing: border-box;
  vertical-align: middle;
  background-color: #fff;
  border: 1px solid #aaa;
  border-radius: 4px;
  height: 1.5rem;
  padding: .25rem .5rem;
  font-size: .85rem;
  line-height: 1.2;
}

button {
  cursor: pointer;
  background-color: #eee;
  margin-top: auto;
}

.error-banner {
  color: #900;
  text-align: center;
  background: #ffe0e0;
  border: 1px solid #c00;
  border-radius: 4px;
  margin-bottom: 1rem;
  padding: .75rem 1rem;
  position: relative;
}

.error-banner button {
  color: #900;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.2rem;
  position: absolute;
  top: .5rem;
  right: 1rem;
}

.board-frame {
  background-color: #7a5230;
  border: 8px solid #7a5230;
  border-radius: 6px;
  max-width: 95vw;
  margin: 0 auto;
  padding: 5px;
  box-shadow: 0 0 10px #00000080;
}

.board {
  background-color: #2f2f2f;
  border-radius: 2px;
  padding: 1rem;
}

.board-controls {
  justify-content: space-between;
  align-items: center;
  margin: 1rem;
  display: flex;
}

.modal-buttons {
  gap: .5rem;
  display: flex;
}

.column-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.modal {
  z-index: 1000;
  background: #0009;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background: #fff;
  border-radius: 1px;
  min-width: 300px;
  max-width: 90vw;
  padding: 1rem 1.5rem;
  position: relative;
  box-shadow: 0 0 15px #00000080;
}

.close-button {
  cursor: pointer;
  color: #555;
  background: none;
  border: none;
  padding: 0;
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1;
  position: absolute;
  top: 0;
  right: .3rem;
}

.close-button:hover {
  color: #000;
}

.filters {
  gap: .5rem;
  display: flex;
}

.task-column {
  color: #fff;
  background-color: #2f2f2f;
  border-radius: 8px;
  flex-direction: column;
  gap: .5rem;
  width: 220px;
  min-height: 400px;
  padding: .5rem;
  display: flex;
}

.task-column h3 {
  text-align: center;
  border-bottom: 1px solid #777;
  padding-bottom: .5rem;
  font-weight: bold;
}

.task-card {
  color: #000;
  background-color: #ffc107;
  width: 190px;
  height: 170px;
  padding: .8rem;
  font-size: .85rem;
  position: relative;
  box-shadow: 2px 2px 4px #00000080;
}

.task-card:after {
  content: "";
  border-top: 0 solid #0000;
  border-bottom: 18px solid #0000001a;
  border-left: 18px solid #0000;
  border-right: 0 solid #0000;
  position: absolute;
  bottom: 0;
  right: 0;
}

.task-card[data-status="new"] {
  background-color: #ff9;
}

.task-card[data-status="in progress"] {
  background-color: #aed7ff;
}

.task-card[data-status="finished"] {
  background-color: #c9f69b;
}

.task-card h4 {
  white-space: nowrap;
  text-overflow: ellipsis;
  margin: 1rem 0 1.5rem;
  font-weight: bold;
  overflow: hidden;
}
